import React from "react";
import { Text, Icon, Center } from "@chakra-ui/react";
import { MdError, MdPlaylistAddCheck, MdBatteryAlert } from "react-icons/md";
import { RiBatterySaverFill } from "react-icons/ri";
import {CardType,NotificationCardProps} from "../../modules/interfaces"

{/*
type CardType =
  | "contract-request"
  | "battery-error"
  | "battery-warning"
  | "battery-request"
  | "action-error"
  | "action-warning";

interface NotificationCardProps {
  type: CardType;
  corporationName: string | undefined;
  departmentName: string | undefined;
  batteryOptionID: string | undefined;
  errorMessage: string | undefined;
  requiredAction: string | undefined;
  errorCode: string | undefined;
}
*/}

const NotificationCard = (props: NotificationCardProps) => {
  const {
    type,
    corporationName,
    departmentName,
    batteryOptionID,
    errorMessage,
    requiredAction,
    errorCode,
  } = props;

  const getCardData = () => {
    switch (type) {
      case "contract-request":
        return {
          color: "#333533",
          iconColor: "#62BB2C",
          icon: MdPlaylistAddCheck,
          text: `\n`,
        };
      case "action-error":
        return {
          color: "#333533",
          iconColor: "#EB5757",
          icon: MdError,
          text: `${errorMessage}\n${requiredAction}\n${errorCode}`,
        };
      case "action-warning":
        return {
          color: "#333533",
          iconColor: "#FFEE32",
          icon: MdError,
          text: `${errorMessage}\n${requiredAction}\n${errorCode}`,
        };
      case "battery-request":
        return {
          color: "#333533",
          iconColor: "#62BB2C",
          icon: RiBatterySaverFill,
          text: `${corporationName}${departmentName} `,
        };
      case "battery-error":
        if (batteryOptionID) {
          return {
            color: "#333533",
            iconColor: "#EB5757",
            icon: MdBatteryAlert,
            text: `${corporationName}${departmentName} ID${batteryOptionID}${errorMessage}`,
          };
        } else {
          return {
            color: "#EB5757",
            iconColor: "#EB5757",
            icon: MdBatteryAlert,
            text: `${errorCode} \n${requiredAction}\n${errorCode}`,
          };
        }
      case "battery-warning":
        if (batteryOptionID) {
          return {
            color: "#333533",
            iconColor: "#EFDB00",
            icon: MdBatteryAlert,
            text: `${corporationName}${departmentName}ID${batteryOptionID}${errorMessage}`,
          };
        } else {
          return {
            color: "#EFDB00",
            iconColor: "#EFDB00",
            icon: MdBatteryAlert,
            text: `${errorCode}\n${requiredAction}\n${errorCode}`,
          };
        }
      default:
        return {
          color: "#333533",
          iconColor: "#333533",
          icon: MdError,
          text: "ERROR",
        };
    }
  };

  const cardData = getCardData();

  return (
    <Center
      w="375px"
      h="93px"
      flexDirection="row"
      color={cardData.iconColor}
      border="1px"
      borderColor={cardData.iconColor}
      padding="15px"
    >
      <Icon w="30px" h="30px" as={cardData.icon} />
      <Text fontSize="14px" color={cardData.color} marginLeft="10px">
        {cardData.text.split("\n").map((str, index) => (
          <React.Fragment key={index}>
            {str}
            <br />
          </React.Fragment>
        ))}
      </Text>
    </Center>
  );
};

export default NotificationCard;
